:root {
  --dark-blue: #323c5b;
}

html * {
  font-family: "Poppins", sans-serif;
}

body {
  margin: 0;
  width: 100vw;
  max-width: 100vw;
  overflow-x: hidden;
}

iframe {
  display: none;
}

.text-secondary {
  color: #6c6c6c;
}

.nav-links .MuiPaper-root {
  min-width: 150px !important;
}

.nav-links .MuiList-root {
  padding-top: 0;
  padding-bottom: 0;
}

.nav-links .MuiButtonBase-root a {
  width: 100%;
  text-decoration: none;
  color: inherit;
}